/* styling borrowed heavily from example in FriendCard activity*/

.card {
  background: #fff;
  border-radius: 2px;
  height: 225px; 
  position: relative;
  width: 225px;
  text-align: left;
}

.img-container {
  height: 50%;
  overflow: hidden;
  text-align: center;
  background:blue;
}
.card > .img-container > img {
  width: 60%
}

.container {
    height: 75px;
}

